body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    margin: 0 auto;
    width: 50vw;
    max-width: 450px;
    margin: 0 auto;
    background: #f2f2f2;
    border-radius: 10px;
    margin-top: 30px;
}

.but_del {
	position: absolute;
	right: 10px;
    top: 10px;
	padding: 0;
    background: none;
    border: none;
}

.but_del img {
	width: 20px;
	cursor: pointer;
}

@-webkit-keyframes moving {
    0% {opacity: .2;}
    100% {opacity: 1;}
}

@keyframes moving {
    0% {opacity: .2;}
    100% {opacity: 1;}
}

.but_del img:hover {
    -webkit-animation-name: moving;
            animation-name: moving;    
    -webkit-animation-duration: 1s;    
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.list_block {
	position: relative;
    background: #fff;
    border-radius: 12px;
    padding: 0 10px;
    padding-bottom: 1px;
    margin: 0 25px; 
    color: #949494;   
}

.list_block h2 {
	padding-right: 25px; 
	color: #181818;  
}
.but_addNew {
    border: none;
    background: none;
    border-radius: 50px;  
    width: 70px; 
    -webkit-transition: -webkit-transform 550ms; 
    transition: -webkit-transform 550ms; 
    transition: transform 550ms; 
    transition: transform 550ms, -webkit-transform 550ms; 
}

.but_addNew img {  
    cursor: pointer;
    margin-right: 10px;
    width: 100%;
    vertical-align: bottom;
}

.but_block {
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    align-items: center;
    color: #62ce76;
    padding: 25px;
}

.active {
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}






.search_block input {
    width: 100%;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    background: no-repeat;
    border: none;
    padding-left: 15px;
    font-size: 16px;
    color: #fff;
}

.search_block {
    display: flex;
    padding: 15px;
    background: #666666;
    border-radius: 10px 10px 0 0;
}

.search_block input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-size: 16px;
    color: #fff    
}
.search_block input::-moz-placeholder, textarea::-moz-placeholder {
    font-size: 16px;
    color: #fff /* Firefox 19+ */    
}
.search_block input:-moz-placeholder, textarea:-moz-placeholder {
    font-size: 16px;
    color: #fff /* Firefox 18- */    
}
.search_block input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    font-size: 16px;
    color: #fff  
}
.inputBlock {
    display: flex;
    flex-direction: column;
    padding: 25px;
    border-top: 1.5px solid #e2e0e0;
}

.inputBlock input {
    padding-left: 15px;
    margin: 5px 0;
    border: none;
    border-radius: 30px;
    height: 50px;
    background: #e8e8e8;
    font-size: 16px;
}

input:focus, button:focus {
    outline: none;
}

button {
    cursor: pointer;
}

.inputBlock input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-size: 16px;    
}
.inputBlock input::-moz-placeholder, textarea::-moz-placeholder {
    font-size: 16px; /* Firefox 19+ */    
}
.inputBlock input:-moz-placeholder, textarea:-moz-placeholder {
    font-size: 16px; /* Firefox 18- */    
}
.inputBlock input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    font-size: 16px;    
}

.but_add {
    margin-top: 15px;
    border: none;
    border-radius: 30px;
    height: 50px;
    background: -webkit-linear-gradient(top, #8cf8a0, #62ce76);
    background: linear-gradient(to bottom, #8cf8a0, #62ce76);
    width: 140px;
    color: #fff;
    box-shadow: 0px 7px 20px 5px rgba(148, 216, 160, 0.5);
}



ul {
	list-style: none;
	padding-left: 0;
}

.mes {
	width: 100%;
	text-align: center;
}

.mes p {
	font-size: 22px;
	color: #a5a5a5;
	font-weight: 400;
}

.example-enter {
    opacity: 0.01;
    height: 0;
}

.example-enter.example-enter-active {
    opacity: 2;
    height: 190px;
    -webkit-transition:  height .3s ease-in, opacity .4s ease-in;
    transition:  height .3s ease-in, opacity .4s ease-in;
}

.example-leave {
    opacity: 2;
    height: 190px;
    -webkit-transition:  height .3s ease-in, opacity .3s ease-in;
    transition:  height .3s ease-in, opacity .3s ease-in;
}

.example-leave.example-leave-active {
    opacity: 0.01;
    height: 0;
}

